.Sections {
  padding: 50px;
  margin: 5px auto;
  text-align: center;
  inline-size: max-content;
}
.Counter {
  width: 400px;
  height: 400px;
  padding: 10px;
  border: 1px solid black;
  border-radius: 50%;
  /* align-items: center; */
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  background: linear-gradient(90deg, #aea4e3, #d3ffe8);
}

.Counter__controls > button {
  border: none;
  padding: 10px 15px;
  background-color: teal;
  color: #fff;
  font-weight: 500;
  margin-left: 5px;
  margin-right: 5px;
  font-family: inherit;
  cursor: pointer;
  outline: none;
  box-shadow: 1px 2px 2px 0 rgb(0 0 0 / 0.5);
  transition: box-shadow 250ms linear;
}

.Counter__controls > button:active {
  box-shadow: 2px 5px 2px 0 rgb(0 0 0 / 0.5);
}

.Counter__value {
  margin-right: 15px;
  margin-left: 15px;
  font-size: 64px;
  font-family: monospace;
  text-align: center;
}
